import { Component, OnInit } from '@angular/core';
import {  FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { MustMatch } from 'src/app/shared/directives/mustmatch.validator';
import { UserService } from 'src/app/shared/services/user.service';
import { routerTransition } from '../router.animations';


@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.scss'],
  animations: [routerTransition()]

})
export class RegisterComponent implements OnInit {
  //user: User=new User();  
  registerForm:FormGroup ;
  // emailRegex = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  // showSucessMessage: boolean;
  serverErrorMessages: string;
  submitted=false;
  constructor(private _userService:UserService, private _router:Router,private _formBilder:FormBuilder ) {
    this.registerForm=this._formBilder.group({
      'fullName': ['',Validators.required],
      'email':['',[Validators.required,Validators.email,Validators.pattern]],
      'password':['',[Validators.minLength(8), Validators.maxLength(30)]],
       'repeatPassword':['',Validators.required]},
        {validator:MustMatch('password','repeatPassword')
    });
   }

  ngOnInit() {
  }
  get f(){return this.registerForm.controls;}


  moveToLogin(){
    this._router.navigate(['/login']);
  }
 onSubmit(fullName,email,password){
   console.log(fullName);
   this.submitted=true;
   this._userService.postUser(fullName,email,password).subscribe(
      res => {
        // console.log('j ai depassé subscribe');
        console.log(res)
              this._router.navigate(['/login']);
      },
      err => { if (err.status === 422) {
        this.serverErrorMessages = err.error.join('<br/>');
        console.log('erreur: '+ this.serverErrorMessages)
        // this.serverErrorMessages =err.error.message;
      }
      else
        this.serverErrorMessages = 'Something went wrong.Please contact admin.';

      });
 
    
  }
}

