import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { User } from '../models/user.model';
import { environment } from 'src/environments/environment';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class UserService {
  private header=new HttpHeaders().set('Content-Type','application/json');
  constructor(private _http:HttpClient){}
  
postUser(fullName,email,password) :Observable<any>{
  const user = {
    fullName: fullName,
    email: email,
    password: password,
  };
     return this._http.post(environment.apiBaseUrl+'/register',user,{headers:this.header});


}
login(email,password){
  const model={
    email: email,
    password: password
  };
  return this._http.post(environment.apiBaseUrl+'/login',model,{headers:this.header})

}
setToken(token:string){
  localStorage.setItem('token',token);
}
 
deleteToken(){
  localStorage.removeItem('token');
}
 getUserPayload(){
   var token=localStorage.getItem('token');
   if(token){
     var userPayload=atob(token.split('.')[1]);
     return JSON.parse(userPayload);

   }else return null;
 }

 isLoggedIn( ){
  var userPayload=this.getUserPayload();
  if(userPayload){ return userPayload.exp> Date.now()/1000;}
 }
  
}
 